body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
html, body {
  margin: 0;
  padding: 0;
  background: #181b21;
  font-family: 'Roboto', sans-serif;
  letter-spacing: -0.23px;
}
.chatApp__loaderWrapper {
  margin: 80px auto;
  text-align: center;
}
.chatApp__loaderText {
  color: #4870df;
  font-weight: bold;
}
.chatApp__loader,
.chatApp__loader::before,
.chatApp__loader::after {
  border-radius: 1em;
  width: 1em;
  height: 1em;
  animation-fill-mode: both;
  animation: loading 1.8s infinite ease-in-out;
}
.chatApp__loader {
  margin: auto;
  color: #4870df;
  font-size: 12px;
  position: relative;
  animation-delay: -0.16s;
}
.chatApp__loader::before,
.chatApp__loader::after {
  content: '';
  position: absolute;
  top: 0;
}
.chatApp__loader::before {
  left: -1.5em;
  animation-delay: -0.32s;
}
.chatApp__loader::after {
  left: 1.5em;
}
.chatApp__room {
  display: flex;
  align-content: flex-start;
  justify-content: center;
  padding: 5rem 20rem;
  animation: fadeChatApp 0.6s ease-in-out;
  animation-fill-mode: forwards;
}
.chatApp__conv {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 0;
  max-width: 400px;
  float: left;
  margin: 0 2.5rem;
  box-shadow: 0 35px 20px -30px rgba(0,0,0,0.1);
}
.chatApp__convTitle {
  display: inline-block;
  margin: 0 0 2rem 0;
  font-size: 18px;
  font-weight: bold;
  color: #f0f0f0;
  text-transform: uppercase;
  position: relative;
}
.chatApp__convTitle::after {
  content: '';
  position: absolute;
  background-color: #4870df;
  width: 100%;
  height: 2px;
  border-radius: 30px;
  left: 0;
  right: 0;
  bottom: -16px;
}
.chatApp__convTimeline {
  display: flex;
  height: 400px;
  padding: 1rem;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  background-color: #f5f5f5;
  flex-direction: column-reverse;
  overflow-y: auto;
}
.chatApp__convMessageItem {
  padding: 0 0 1rem 0;
  opacity: 0;
  transition: all 0.15s ease-in-out;
  animation: fadeNewMessage 0.5s;
  animation-fill-mode: forwards;
}
.chatApp__convMessageAvatar {
  height: 20px;
  font-size: 13px;
  font-weight: 500;
  border-radius: 40px;
  margin-top: 5px;
  image-rendering: -webkit-optimize-contrast;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.chatApp__convMessageValue {
  position: relative;
  max-width: 50%;
  min-height: 40px;
  padding: 0.75rem;
  border-radius: 10px;
  font-size: 14px;
  line-height: 18px;
  font-weight: 500;
  box-shadow: 0px 10px 10px -8px rgba(0,0,0,0.08);
  box-sizing: border-box;
  overflow: hidden;
  text-overflow: ellipsis;
}
.chatApp__convMessageTime {
  margin-top: 4px;
  text-transform: lowercase;
  font-size: 11px;
  color: #999;
}
.chatApp__convMessageItem:first-child {
  padding-bottom: 0;
}
.chatApp__convMessageItem--right + .chatApp__convMessageItem--right {
  padding-bottom: 5px;
}
.chatApp__convMessageItem--left + .chatApp__convMessageItem--left {
  padding-bottom: 5px;
}
.chatApp__convMessageItem--left + .chatApp__convMessageItem--left .chatApp__convMessageAvatar {
  display: none;
}
.chatApp__convMessageItem--right + .chatApp__convMessageItem--right .chatApp__convMessageAvatar {
  display: none;
}
.chatApp__convMessageItem--left .chatApp__convMessageValue {
  text-align: left;
  background-color: #fff;
  border-bottom-left-radius: 0;
}
.chatApp__convMessageItem--left .chatApp__convMessageValue::before {
  /*content: '';*/
  position: absolute;
  top: 14px;
  left: -8px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 6px 10px 6px 0;
  border-color: transparent #4870df transparent transparent;
}
.chatApp__convMessageItem--left {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.chatApp__convMessageItem--right {
  display: flex;
  align-items: flex-end;
  flex-direction: column;
}
.chatApp__convMessageItem--right .chatApp__convMessageValue {
  float: right;
  text-align: right;
  color: #fff;
  background-color: #1cb684;
  border-bottom-right-radius: 0;
}
.chatApp__convMessageItem--right .chatApp__convMessageValue::after {
  /*content: '';*/
  position: absolute;
  top: 14px;
  right: -8px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 6px 0 6px 10px;
  border-color: transparent transparent transparent #fafafa;
}
.chatApp__convMessageItem--left .chatApp__convMessageValue a {
  color: #91ffb1;
}
.chatApp__convMessageItem--left .chatApp__convMessageValue a:hover,
.chatApp__convMessageItem--left .chatApp__convMessageValue a:focus {
  color: #75ff9e;
}
.chatApp__convMessageItem--right .chatApp__convMessageValue a {
  color: #09f;
}
.chatApp__convMessageItem--right .chatApp__convMessageValue a:hover,
.chatApp__convMessageItem--right .chatApp__convMessageValue a:focus {
  color: #007dd1;
}
.chatApp__convSendMessage {
  min-height: 63px;
  position: relative;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  background-color: #fff;
  box-sizing: border-box;
}
.chatApp__convSendMessage:empty {
  background-color: #f5f5f5;
}
.chatApp__convSendMessage {
  padding: 1rem;
}
.end-chat {
  margin-left: auto;
  cursor: pointer;
  padding-bottom: 10px;
  margin-bottom: -10px;
}
.end-chat .fas {
  color: #1cb684;
  margin-top: 13px;
}
.end-chat .end-chat-btn {
  position: absolute;
  right: 10px;
  top: 40px;
  border: none;
  padding: 13px 15px;
  border-radius: 5px;
  box-shadow: 0 0 5px #e6e6e6;
  display: none;
  cursor: pointer;
  font-size: 12px;
}
.end-chat .end-chat-btn:focus {
  outline: none;
}
.end-chat .end-chat-btn .fas {
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  color: #1cb684;
  margin-right: 6px;
  font-size: 11px;
  margin-top: 0;
}
.end-chat:hover {
  padding-left: 20px;
}
.end-chat:hover .end-chat-btn {
  display: block;
}
.css-2b097c-container.css-2b097c-container input,
.css-2b097c-container.css-2b097c-container input:focus,
.css-2b097c-container.css-2b097c-container input:active,
.css-2b097c-container.css-2b097c-container div,
.css-2b097c-container.css-2b097c-container div:focus,
.css-2b097c-container.css-2b097c-container div:active,
.css-2b097c-container.css-2b097c-container span,
.css-2b097c-container.css-2b097c-container span:focus,
.css-2b097c-container.css-2b097c-container span:active {
  outline: none;
  border: none;
}
.css-2b097c-container.css-2b097c-container .css-1laao21-a11yText,
.css-2b097c-container.css-2b097c-container .css-1laao21-a11yText:focus,
.css-2b097c-container.css-2b097c-container .css-1laao21-a11yText:active,
.css-2b097c-container.css-2b097c-container .css-1laao21-a11yText span:focus,
.css-2b097c-container.css-2b097c-container .css-1laao21-a11yText:focus span:focus
.css-2b097c-container.css-2b097c-container .css-1laao21-a11yText div:focus {
  outline: none !important;
  border: none !important;
}
.css-yk16xz-control.css-yk16xz-control {
  border: none;
}
.css-1okebmr-indicatorSeparator {
  display: none;
}
.chatApp__convInput {
  float: left;
  height: 30px;
  border: none;
  padding: 0.5rem;
  /*border-radius: 40px;*/
  margin: 0 0.5rem 0 0;
  width: calc(100% - 42px);
  /*background-color: #dedfed;*/
  /*box-shadow: inset 0 0 0 2px #dedfed;*/
  font-size: 14px;
  /*font-family: 'Quicksand', sans-serif;*/
  font-weight: 500;
  transition: 0.15s all ease-in-out;
  box-sizing: border-box;
}
.chatApp__convInput::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #b1b1b1;
  opacity: 1;
}
.chatApp__convInput:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #b1b1b1;
}
.chatApp__convInput::-ms-input-placeholder { /* Microsoft Edge */
  color: #b1b1b1;
}
.chatApp__convInput:focus, .chatApp__convInput:active {
  /*box-shadow: inset 0 0 0 2px #7690d6;*/
}
.chatApp__convInput:focus {
  outline: none;
}
.chatApp__convButton {
  float: right;
  position: relative;
  width: 30px;
  height: 30px;
  cursor: pointer;
  padding: 5px;
  border-radius: 30px;
  color: #fff;
  background-color: #1cb684;
  text-align: center;
  box-shadow: 0px 14px 10px -8px rgba(0,0,0,0.2);
  transition: 0.15s all ease-in-out;
  box-sizing: border-box;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.chatApp__convButton:hover {
  background-color: #3c559c;
}
.chatApp__convButton i {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  padding: 0 0 0 1px;
  font-size: 20px;
}
.chatApp__convButton--loading {
  cursor: wait;
  background-color: #4870df;
}
.chatApp__convButton--loading:hover {
  background-color: #4870df;
}
.chatApp__convButton--loading::before, .chatApp__convButton--loading::after {
  content: '';
  position: absolute;
  z-index: 1;
  display: block;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 8px;
  height: 8px;
  margin: auto;
  border-radius: 40px;
  background-color: #fff;
}
.chatApp__convButton--loading::after {
  animation: loadSendMessage 1.5s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
}
.chatApp__convButton--loading i {
  display: none;
}
.chatApp__convSendMessage--typing .chatApp__convTyping {
  display: block;
}
.chatApp__convTyping {
  position: absolute;
  top: 0;
  font-size: 10px;
  font-weight: bold;
}
.chatApp__convTypingDot {
  position: relative;
  left: 4px;
  display: inline-block;
  width: 2px;
  height: 2px;
  background-color: #606060;
  animation: typingMessage 1.5s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}
.chatApp__convTypingDot::before, .chatApp__convTypingDot::after {
  content: '';
  position: absolute;
  display: inline-block;
  width: 2px;
  height: 2px;
  background-color: #606060;
  animation: typingMessage 1.5s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}
.chatApp__convTypingDot::before {
  left: 4px;
  animation-delay: 0.5s;
}
.chatApp__convTypingDot::after {
  left: 8px;
  animation-delay: 1s;
}
.chatApp__convChannelLine {
  margin: -1rem;
  background: white;
  cursor: pointer;
  font-size: 12px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-top: 32px;
}
.channelItem {
  background: #fff;
  margin-bottom: 1px;
  display: block;
  padding: 8px 1rem;
  box-shadow: 0px 1px 0 #f5f5f5;
}
.channelItem:not(.archived):first-child:before {
  position: relative;
  content: 'Active chats';
  display: block;
  font-size: 12px;
  color: #1cb684;
  text-transform: uppercase;
  background: white;
  top: -10px;
  padding-top: 10px;
  margin-left: -15px;
  padding-left: 15px;
  width: calc(100% + 15px);
}
.channelItem.archived {
  background: #f3f3f3;
  box-shadow: 0px 1px 0 #e6e6e6;
  order: 99;
}
.channelItem.archived:before {
  position: relative;
  content: attr(data-header);
  display: block;
  font-size: 12px;
  color: #444;
  text-transform: uppercase;
  background: #f3f3f3;
  top: -10px;
}
.channelItem.archived[data-header]:before {
  padding-top: 10px;
  margin-left: -15px;
  padding-left: 15px;
  width: calc(100% + 15px);
}
.channelItem:hover {
  position: relative;
  background-color: #1cb68417;
}
.channelName {
  float: right;
  display: inline-block;
  font-size: 11px;
  color: #1cb684;
  opacity: .5;
}
.channelBody {
  display: inline-block;
}
.channelTime {
  float: right;
}
.channelAuthor {
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 2px;
}
.channelBody,
.channelTime {
  font-size: 11px;
  color: #999;
}
.start-chat-wrapper {
  display: flex;
  justify-content: center;
}
.start-chat {

}
.btn {
  background: #1cb684;
  padding: 8px 32px;
  border-radius: 20px;
  color: #fff;
  cursor: pointer;
  border: none;
  font-size: 12px;
  font-weight: 700;
}
.btn:hover,
.btn:focus {
  outline: none;
  border: none;
  opacity: .8;
}
@media screen and (max-width:768px) {
  .chatApp__room { flex-wrap: wrap; padding: 2rem 1rem; }
  .chatApp__conv { flex-basis: 100%; min-width: auto; width: 100%; margin: 0; }
  .chatApp__conv:first-of-type { margin: 0 0 4rem 0; }
}
@keyframes loading {
  0%, 80%, 100% { box-shadow: 0 1em 0 -1.3em; }
  40% { box-shadow: 0 1em 0 0; }
}
@keyframes fadeChatApp {
  0% { opacity: 0; transform: translateY(1rem); }
  100% { opacity: 1; transform: translateY(0px); }
}
@keyframes fadeNewMessage {
  0% { opacity: 0; transform: translateY(1rem); }
  100% { opacity: 1; transform: translateY(0px); }
}
@keyframes loadSendMessage {
  0% { opacity: 1; width: 4px; height: 4px; }
  100% { opacity: 0; width: 100%; height: 100%; }
}
@keyframes typingMessage {
  0% { background-color: #606060; }
  50% { background-color: #fff; }
  60% { background-color: #606060; }
}